<template lang="">
  <div
    class="v3scroll-box"
    :ref="`v3box-${id}`"
    :style="`width:${theWidth};height:${theHeight}`"
  >
    <div
      v-if="!noVer && !isMobile"
      @mousedown.stop="onTrackMousedown($event, 1)"
      @mouseenter.stop="hoverH = true"
      @mouseleave.stop="hoverH = false"
      :style="`background-color:${trackColor};opacity:${autoHide ? 0 : 0.8}`"
      :class="[
        'v3-scroll-vertical-track-h',
        { disabled: !isShowH },
        { show: barHDown },
        { left: left },
      ]"
    >
      <div
        @mousedown.stop="onBarMousedown($event, 1)"
        :ref="`v3barh-${id}`"
        :style="
          `transition:width 250ms,height 250ms;background-color:${thumbColor};width:${
            hoverH || barHDown ? Number(breadth) + 4 : Number(breadth)
          }px;height: ${barHTall}px;transform: translateY(${barHScrollTop}px);border-radius:${breadth}px`
        "
      />
    </div>
    <!--  -->
    <div
      v-if="!noHor && !isMobile"
      @mousedown.stop="onTrackMousedown($event, 2)"
      @mouseenter.stop="hoverW = true"
      @mouseleave.stop="hoverW = false"
      :style="`background-color:${trackColor};opacity:${autoHide ? 0 : 0.8}`"
      :class="[
        'v3-scroll-vertical-track-w',
        { disabled: !isShowW },
        { show: barWDown },
        { top: top },
      ]"
    >
      <div
        @mousedown.stop="onBarMousedown($event, 2)"
        :ref="`v3barw-${id}`"
        :style="
          `transition:height 250ms,width 250ms;background-color:${thumbColor};height:${
            hoverW || barWDown ? Number(breadth) + 4 : Number(breadth)
          }px;width: ${barWTall}px;transform: translateX(${barWScrollLeft}px)`
        "
      />
    </div>
    <!--  -->
    <div
      :ref="`v3body-${id}`"
      :class="[
        'v3-scroll-body',
        { isPc: !isMobile },
        { isAndroid: isAndroid },
        { isSmooth: needSmooth },
      ]"
      :style="
        `${noVer ? 'height:100%;overflow-y:hidden;' : ''} ${
          noHor ? 'width:100%;overflow-x:hidden;' : ''
        }`
      "
      tabindex="9999"
      @scroll="onScrollEvent"
    >
      <div class="v3-scroll-content" :ref="`v3bodylistener-${id}`">
        <slot></slot>
      </div>
    </div>
  </div>
</template>
<script>
import ElementResizeDetectorMaker from 'element-resize-detector';
let canEmited = 1;
export default {
  name: 'v3scroll',
  data() {
    return {
      isMobile: false,
      isAndroid: false,
      observer: null, // 
      isShowH: false, // 
      isShowW: false, // ,
      trickH: 0, //  = box - padding4px
      trickW: 0, // 
      barHTall: 0, // bar
      barWTall: 0, // bar
      barHScrollTop: 0, // Y
      barWScrollLeft: 0, // X
      startBarHScrollTop: 0, // Y
      startBarWScrollLeft: 0, // X
      barHDown: false, // 
      barWDown: false, // 
      startX: 0, // X
      startY: 0, // Y
      scaleH: 0, // 
      scaleW: 0,
      hoverH: false, // H
      hoverW: false, // W
      timer: 0, // timer
      reqTimer: null, // requestAnimationFrametimer
      needSmooth: true, //  
      isBehavior: false, // 
    };
  },
  mounted() {
    // 
    this.isMobile = /(android)|(iphone)|(symbianos)|(windows phone)|(ipad)|(ipod)/.test(
      navigator.userAgent.toLowerCase()
    );
    this.isAndroid = /android/.test(navigator.userAgent.toLowerCase());

    // ,chrome/firefox/opera
    this.isBehavior = 'scroll-behavior' in document.body.style;

    // 
    this.callback();
    this.listenResize();

    if (!this.isMobile) {
      // 
      document.addEventListener('mousemove', this.onBarDragMove, false);
      document.addEventListener('mouseup', this.onMouseUp, false);
    }
  },
  beforeUnmount() {
    // 
    if (!this.isMobile) {
      document.removeEventListener('mousemove', this.onBarDragMove, false);
      document.removeEventListener('mouseup', this.onMouseUp, false);
    }

    if (window.ResizeObserver) {
      this.observer.disconnect();
    } else {
      this.observer.uninstall(this.$refs[`v3body-${this.id}`]);
    }
    this.observer = null;
  },

  computed: {
    realShowH() {
      return this.isShowH && !this.noVer;
    },
    realShowW() {
      return this.isShowW && !this.noHor;
    },
    theWidth() {
      return Number(this.width) ? `${this.width}px` : this.width;
    },
    theHeight() {
      return Number(this.height) ? `${this.height}px` : this.height;
    },
  },
  watch: {
    noVer(newV) {
      if (newV) {
        const dom = this.$refs[`v3body-${this.id}`];
        this.barHScrollTop = 0;
        this.scaleH = 0;
        dom.scrollTop = 0;
      } else {
        this.callback();
      }
    },
    noHor(newV) {
      if (newV) {
        const dom = this.$refs[`v3body-${this.id}`];
        this.barWScrollLeft = 0;
        this.scaleW = 0;
        dom.scrollLeft = 0;
      } else {
        this.callback();
      }
    },
    // ,  canEmitedsafari
    barHScrollTop(newV) {
      if (newV < 0 || newV > this.trickH - this.barHTall) {
        return;
      }
      const dom = this.$refs[`v3body-${this.id}`];
      const p = {
        offsetHeight: dom.offsetHeight,
        offsetWidth: dom.offsetWidth,
        clientHeight: dom.clientHeight,
        clientWidth: dom.clientWidth,
        scrollHeight: dom.scrollHeight,
        scrollWidth: dom.scrollWidth,
        scrollTop: dom.scrollTop,
        scrollLeft: dom.scrollLeft,
      };
      if (canEmited) {
        if (newV === 0) {
          canEmited = false;
          this.$emit('onVerStart', p, dom);
        } else if (newV === this.trickH - this.barHTall) {
          this.$emit('onVerEnd', p, dom);
          canEmited = false;
        }
      } else {
        canEmited = true;
      }
      this.$emit('onScroll', p, dom);
    },

    // 
    barWScrollLeft(newV) {
      if (newV < 0 || newV > this.trickW - this.barWTall) {
        return;
      }
      const dom = this.$refs[`v3body-${this.id}`];
      const p = {
        offsetHeight: dom.offsetHeight,
        offsetWidth: dom.offsetWidth,
        clientHeight: dom.clientHeight,
        clientWidth: dom.clientWidth,
        scrollHeight: dom.scrollHeight,
        scrollWidth: dom.scrollWidth,
        scrollTop: dom.scrollTop,
        scrollLeft: dom.scrollLeft,
      };
      if (canEmited) {
        if (newV === 0) {
          canEmited = false;
          this.$emit('onHorStart', p, dom);
        } else if (newV === this.trickW - this.barWTall) {
          canEmited = false;
          this.$emit('onHorEnd', p, dom);
        }
      } else {
        canEmited = true;
      }

      this.$emit('onScroll', p, dom);
    },
  },
  props: {
    id: { type: [String, Number], default: () => Date.now() + Math.random() },
    noVer: { type: Boolean, default: false }, // 
    noHor: { type: Boolean, default: false }, // 
    left: { type: Boolean, default: false }, // 
    top: { type: Boolean, default: false }, // 
    breadth: { type: String, default: '6' }, // bar
    trackColor: { type: String, default: 'transparent' }, // 
    thumbColor: { type: String, default: '#7f7f7f' }, // 
    autoHide: { type: Boolean, default: true }, // 
    minLength: { type: Number, default: 20 }, // 
    width: { type: [Number, String], default: '100%' }, // ohyeah
    height: { type: [Number, String], default: '100%' }, // ohyeah
    resizeObject: { type: Boolean, default: false }, // resizescroll
  },
  methods: {
    onScrollEvent() {
      const dom = this.$refs[`v3body-${this.id}`];
      if (this.realShowH) {
        this.barHScrollTop = dom.scrollTop / this.scaleH;
      }
      if (this.realShowW) {
        this.barWScrollLeft = dom.scrollLeft / this.scaleW;
      }
    },
    listenResize() {
      // ResizeObserver
      if (window.ResizeObserver) {
        this.observer = new ResizeObserver(this.callback);
        this.observer.observe(this.$refs[`v3body-${this.id}`]);
        this.observer.observe(this.$refs[`v3bodylistener-${this.id}`]);
      } else {
        this.observer = ElementResizeDetectorMaker(
          this.resizeObject ? null : { strategy: 'scroll' }
        );
        this.observer.listenTo(this.$refs[`v3body-${this.id}`], this.callback);
        this.observer.listenTo(
          this.$refs[`v3bodylistener-${this.id}`],
          this.callback
        );
      }
    },
    callback() {
      const dom = this.$refs[`v3body-${this.id}`];
      const b = dom.getBoundingClientRect(); // boxbody
      this.trickH = b.height - 4; //  = box - padding4px
      this.trickW = b.width - 4;

      this.isShowH = dom.scrollHeight > dom.clientHeight; //  > 
      this.isShowW = dom.scrollWidth > dom.clientWidth;

      if (this.realShowH) {
        //  = / * 
        this.barHTall = Math.max(
          (dom.clientHeight / dom.scrollHeight) * this.trickH,
          this.minLength > dom.clientHeight ? 0 : this.minLength
        );
        //  =  - /  - 
        this.scaleH =
          (dom.scrollHeight - dom.clientHeight) / (this.trickH - this.barHTall);
        this.barHScrollTop = Math.min(
          Math.max(dom.scrollTop / this.scaleH, 0),
          this.trickH - this.barHTall
        );
      } else {
        this.scaleH = 0;
        dom.scrollTop = 0;
      }
      if (this.realShowW) {
        this.barWTall = Math.max(
          (dom.clientWidth / dom.scrollWidth) * this.trickW,
          this.minLength > dom.clientWidth ? 0 : this.minLength
        );
        this.scaleW =
          (dom.scrollWidth - dom.clientWidth) / (this.trickW - this.barWTall);
        this.barWScrollLeft = Math.min(
          Math.max(dom.scrollLeft / this.scaleW, 0),
          this.trickW - this.barWTall
        );
        dom.scrollLeft = this.barWScrollLeft * this.scaleW;
      } else {
        this.scaleW = 0;
        dom.scrollLeft = 0;
      }
    },
    /**
     * 
     * @parame e 
     * @params type 12
     * @params targetNum 
     * */
    onBarMousedown(e, type, targetNum) {
      e.preventDefault();
      // 
      const isTargetSmooth = targetNum || targetNum === 0;
      //   smooth
      if (!isTargetSmooth || (isTargetSmooth && !this.isBehavior)) {
        this.needSmooth = false;
      }

      this.barHDown = type === 1;
      this.barWDown = type === 2;
      this.startX = e.clientX;
      this.startY = e.clientY;
      this.startBarHScrollTop =
        this.barHDown && isTargetSmooth ? targetNum : this.barHScrollTop; // 
      this.startBarWScrollLeft =
        this.barWDown && isTargetSmooth ? targetNum : this.barWScrollLeft;
    },

    /**
     * 
     * @param e 
     * @param type 1/2
     * */
    onTrackMousedown(e, type) {
      e.preventDefault();
      // this.needSmooth = false;
      const dom = this.$refs[`v3body-${this.id}`];
      let temp;
      this.$nextTick(() => {
        let targetNum;
        if (type === 1) {
          //  temp
          if (this.barHScrollTop >= e.offsetY) {
            temp = Math.max(e.offsetY - 6, 0);
          } else {
            temp = Math.min(
              e.offsetY - this.barHTall + 2,
              this.trickH - this.barHTall
            );
          }

          if (this.isBehavior) {
            dom.scrollTop = temp * this.scaleH;
          } else {
            targetNum = temp * this.scaleH;
            //  / 15  256ms
            cancelAnimationFrame(this.reqTimer);
            this.smoothScrollTo(
              'scrollTop',
              dom,
              targetNum,
              (targetNum - dom.scrollTop) / 15
            );
          }
        }
        if (type === 2) {
          // 
          if (this.barWScrollLeft >= e.offsetX) {
            temp = Math.max(e.offsetX - 6, 0);
          } else {
            temp = Math.min(
              e.offsetX - this.barWTall + 2,
              this.trickW - this.barWTall
            );
          }

          if (this.isBehavior) {
            dom.scrollLeft = temp * this.scaleW;
          } else {
            targetNum = temp * this.scaleW;
            cancelAnimationFrame(this.reqTimer);
            this.smoothScrollTo(
              'scrollLeft',
              dom,
              targetNum,
              (targetNum - dom.scrollLeft) / 15
            );
          }
        }
        // 
        this.onBarMousedown(e, type, temp);
      });
    },

    /**
     * 
     * @param e 
     * */
    onBarDragMove(e) {
      const dom = this.$refs[`v3body-${this.id}`];
      //console.log(dom);

      if (this.barHDown) {
        this.needSmooth = false;
        if (!this.timer) {
          cancelAnimationFrame(this.reqTimer);
          requestAnimationFrame(() => {
            const temp = Math.min(
              Math.max(this.startBarHScrollTop + e.clientY - this.startY, 0),
              this.trickH - this.barHTall
            );
            dom.scrollTop = temp * this.scaleH;
            this.timer = 0;
          });
          this.timer = 1;
        }
      } else if (this.barWDown) {
        this.needSmooth = false;
        if (!this.timer) {
          cancelAnimationFrame(this.reqTimer);
          requestAnimationFrame(() => {
            const temp = Math.min(
              Math.max(this.startBarWScrollLeft + e.clientX - this.startX, 0),
              this.trickW - this.barWTall
            );
            dom.scrollLeft = temp * this.scaleW;
            this.timer = 0;
          });
          this.timer = 1;
        }
      }
    },

    // 
    onMouseUp() {
      this.barHDown = this.barWDown = false;
      this.needSmooth = true;
    },

    /**
     * scrollTo
     * @param type scrollTop/scrollLeft
     * @param dom DOM
     * @param targetNum 
     * @param step 
     * */
    smoothScrollTo(type, dom, targetNum, step) {
      this.reqTimer = requestAnimationFrame(() => {
        if (
          (step < 0 && dom[type] > targetNum) ||
          (step > 0 && dom[type] < targetNum)
        ) {
          dom[type] = dom[type] + step;
          if (Math.abs(targetNum - dom[type]) < Math.abs(step)) {
            this.smoothScrollTo(
              type,
              dom,
              targetNum,
              Math.abs(targetNum - dom[type]) * (step < 0 ? -1 : 1)
            );
          } else {
            this.smoothScrollTo(type, dom, targetNum, step);
          }
        }
      });
    },

    /**
     * 
     * @param x null/'end'
     * @param y null/'end'
     * @param smooth 
     * */
    scrollTo(x, y, smooth) {
      const dom = this.$refs[`v3body-${this.id}`];
      const s_y =
        y === 'end'
          ? dom.scrollHeight - dom.clientHeight
          : y && this.scaleH > 0
          ? y
          : 0;
      const s_x =
        x === 'end'
          ? dom.scrollWidth - dom.clientWidth
          : x && this.scaleW > 0
          ? x
          : 0;
      this.needSmooth = !!smooth;
      this.$nextTick(() => {
        if (this.realShowH && (y || y === 0)) {
          dom.scrollTop = s_y;
        }
        if (this.realShowW && (x || x === 0)) {
          dom.scrollLeft = s_x;
        }
        // firefox scrollTopscroll,
        if (!smooth) {
          this.onScrollEvent();
        }
      });
    },
    /**
     * dom
     * */
    getScrollInfo() {
      const dom = this.$refs[`v3body-${this.id}`];
      return {
        height: dom.clientHeight,
        width: dom.clientWidth,
        clientHeight: dom.clientHeight,
        clientWidth: dom.clientWidth,
        offsetHeight: dom.offsetHeight,
        offsetWidth: dom.offsetWidth,
        scrollTop: dom.scrollTop,
        scrollLeft: dom.scrollLeft,
        scrollHeight: dom.scrollHeight,
        scrollWidth: dom.scrollWidth,
      };
    },
  },
};
</script>
<style lang="less">
.v3scroll-box {
  height: 100vh;
  width: 100vh;

  outline: none;

  .v3-scroll-body {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    outline: none;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    * {
      -ms-overflow-style: auto;
    }
    /* PC  */
    &.isPc {
      -ms-overflow-style: none !important; // IE/Edge
      scrollbar-width: none; // Firefox
      // Chrome/Safari/Opera
      &::-webkit-scrollbar {
        display: none;
      }
    }
    /*  dom */
    &.isAndroid {
      &::-webkit-scrollbar {
        width: 2px;
        height: 2px;
        background-color: transparent;
      }
      &::-webkit-scrollbar-thumb {
        background-color: rgba(200, 200, 200, 0.4);
      }
    }

    &.isSmooth {
      scroll-behavior: smooth;
    }
  }

  &:hover {
    & > .v3-scroll-vertical-track-h,
    & > .v3-scroll-vertical-track-w {
      opacity: 1 !important;
    }
  }
  .disabled {
    visibility: hidden;
    pointer-events: none;
  }

  .v3-scroll-vertical-track-h {
    position: absolute;
    box-sizing: border-box;
    height: 100%;
    right: 0;
    top: 0;
    z-index: 10;
    padding: 2px;
    cursor: pointer;
    transition: opacity 200ms, width 200ms;
    &.show {
      opacity: 1 !important;
    }
    &.left {
      left: 0;
      right: auto;
    }
    & > div {
      border-radius: 999px;
    }
  }
  .v3-scroll-vertical-track-w {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    bottom: 0;
    left: 0;
    z-index: 9;
    padding: 2px;
    cursor: pointer;
    transition: opacity 200ms, height 200ms;
    &.show {
      opacity: 1 !important;
    }
    &.top {
      top: 0;
      bottom: auto;
    }
    & > div {
      border-radius: 999px;
    }
  }
}
</style>
